#main-content {
  padding-top: 56px;
}

.example-box {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  overflow: auto;
  background: #e0e0e0;
}

.example-box-header {
  padding: 1rem;
  margin-bottom: 0.5rem;
  background: var(--u-surface-color);
}
.example-box-header .u-toolbar {
  width: 100%;
}
.example-box-header .u-dropdown {
  display: inline-block;
}

.limited-height {
  max-height: 300px;
  overflow: auto;
}

.example-step-content {
  width: 100%;
  height: 300px;
  background: #ccc;
}

.list-example-box-header .u-list {
  width: 300px;
}

.menu-example .example-box-header {
  height: 14rem;
}

.u-card-filled,
.u-card-outlined,
.u-card-elevated {
  margin-bottom: 8px;
}

.u-card-media,
.u-card-media-wide {
  background: url("/img/venice.jpg") center;
  background-size: cover;
}

.u-list-item-avatar {
  background: var(--u-low-emphasis-color);
  background-size: cover;
}

.chapter,
.article-title {
  margin-top: 80px;
}

.chapter-title,
.article-title {
  margin-bottom: 48px;
  font-size: 2rem;
  font-weight: 400;
  color: var(--u-primary-color);
}

.article-title {
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

pre {
  margin: 0;
}

code {
  padding: 1rem !important;
  unicode-bidi: plaintext;
  background: transparent !important;
}

.nested-menu-items {
  max-height: 0;
  padding-inline-start: 12px;
  overflow: hidden;
}
.nested-menu-items.expanded {
  max-height: 500px;
}

.example-card > .u-card-content:first-child {
  padding: 36px;
}
.example-card .u-floating-action-area {
  position: static;
}
.example-card .u-floating-menu {
  position: relative;
  margin-top: 200px;
}
.example-card .u-chip-avatar {
  background: rgba(0, 0, 0, 0.54);
}

.example-card-code {
  background-color: var(--u-surface-variant-color);
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  border: 2px solid transparent;
  background-color: rgba(0, 0, 0, 0.47);
  background-clip: content-box;
  border-radius: 3px;
}

.u-dark-mode::-webkit-scrollbar-thumb,
.u-dark-mode ::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.47);
}

body {
  overflow-y: auto;
  overflow-y: overlay;
}